.fade-enter {
    margin-top: 30px;
    opacity: .01;
}

.fade-enter.fade-enter-active {
    margin-top: 0px;
    opacity: 1;
    -webkit-transition: opacity .5s ease, margin .5s ease;
    -o-transition: opacity .5s ease, margin .5s ease;
    transition: opacity .5s ease, margin .5s ease;
}

.fade-exit {
    margin-top: 0px;
    opacity: 1;
}

.fade-exit.fade-exit-active {
    margin-top: -30px;
    opacity: 0.01;
    -webkit-transition: opacity .3s ease, margin .5s ease;
    -o-transition: opacity .3s ease, margin .5s ease;
    transition: opacity .3s ease, margin .5s ease;
}



